.Header {
  display: flex;
  grid-template-columns: 1fr 1fr 1fr;
  width: 100%;
  height: 100px;
  align-items: center;
  justify-content: center;
}

.Header-titles {
  display: inline;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
}

.header-title {
  padding: 20px;
  color: #000;
}

.header-title a {
  color: #000;
  text-decoration: none;
  outline: none;
}
